body { background: #050505; }

.header { height: 100vh; }

.header #myVideo { margin: 0 auto; position: absolute; right: 0; bottom: 0; min-width: 100%; min-height: 100%; }

.header .wrapper { position: absolute; top: 0; right: 50%; display: flex; justify-content: space-around; align-items: center; flex-direction: column; background: rgba(0, 0, 0, 0.6); width: 100%; padding: 0 15%; height: 100%; transform: translate(50%); }

.header .wrapper .login-form { position: relative; top: 5px; }

.header .wrapper .login-form p { text-transform: uppercase; font-family: "Roboto", sans-serif; font-size: 15px; font-weight: 500; font-stretch: normal; font-style: normal; line-height: 1.6; letter-spacing: 1.5px; text-align: left; color: white; }

.header .wrapper .login-form p a { color: inherit; text-decoration: none; }

@media screen and (min-width: 500px) { .header .wrapper .login-form { order: 1; align-self: flex-start; position: absolute; right: 9%; top: 81px; } }

.header .wrapper .icons { flex: 1; display: flex; justify-content: space-around; flex-direction: row; align-items: center; width: 100%; }

.header .wrapper .icons a { width: 49px; height: 49px; transition: transform 0.5s linear; transform: scale(1); }

.header .wrapper .icons a img { width: 100%; height: auto; }

@media screen and (min-width: 500px) { .header .wrapper .icons a { margin: 0 14px; width: 47px; height: 47px; } }

.header .wrapper .icons a:hover { transform: scale(1.2); }

@media screen and (min-width: 500px) { .header .wrapper .icons { order: 1; flex: 0; align-self: flex-end; } }

.header .wrapper .logo { flex: 1; display: flex; align-items: flex-end; }

.header .wrapper .logo img { width: 236px; height: 71px; }

@media screen and (min-width: 500px) { .header .wrapper .logo { flex: 0; order: 1; align-self: flex-start; position: absolute; left: 9%; top: 81px; } }

.header .wrapper .arrow { position: absolute; bottom: 31px; }

.header .wrapper .arrow img { transition: transform 1s; transform: scale(1.5); width: 31px; height: 19px; animation-play-state: running; animation: scale 1s infinite linear both; }

.header .wrapper .arrow img:hover { animation-play-state: paused; }

@keyframes scale { 0% { transform: scale(1.5); }
  50% { transform: scale(2); }
  100% { transform: scale(1.5); } }

@media screen and (min-width: 500px) { .header .wrapper .arrow { position: initial; bottom: initial; align-self: flex-end; margin-left: 5%; } }

@media screen and (min-width: 500px) { .header .wrapper { padding: 0 9%; flex-direction: row; justify-content: space-between; padding-bottom: 41px; } }

.main-content { width: 100%; background: #050505; }

.main-content .navigation-panel { display: flex; padding: 56px 0; flex-direction: column; }

.main-content .navigation-panel a { text-transform: uppercase; font-family: "Roboto", sans-serif; font-size: 30px; font-weight: 500; font-stretch: normal; font-style: normal; line-height: 1.6; letter-spacing: 1.5px; text-align: left; color: #202020; }

@media screen and (min-width: 500px) { .main-content .navigation-panel a { font-size: 15px; font-weight: 500; font-stretch: normal; font-style: normal; line-height: normal; letter-spacing: 0.6px; text-align: left; margin-right: 40px; } }

@media screen and (min-width: 500px) { .main-content .navigation-panel { display: flex; flex-direction: row; } }

.block-content { max-width: 500px; margin: 0 auto; margin-bottom: 75px; }

.block-content .block-with-employee { width: 100%; flex-direction: column; padding: 1px 0; }

.block-content .block-with-employee .parent { margin: 6px 0; width: 100%; height: 100%; overflow: hidden; }

.block-content .block-with-employee .parent .child { min-height: 300px; position: relative; width: 100%; height: 100%; background-image: url("../img/image.jpg"); background-position: center; background-repeat: no-repeat; background-size: 200%; transition: all 0.5s; }

.block-content .block-with-employee .parent .child .text { padding: 15%; transition: all 0.5s; display: flex; flex-direction: column; justify-content: center; align-items: center; width: 100%; height: 100%; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); }

.block-content .block-with-employee .parent .child .text h3 { margin: 5px 0; text-transform: uppercase; font-family: "Roboto", sans-serif; font-size: 30px; font-weight: 400; font-stretch: normal; font-style: normal; line-height: 1.6; letter-spacing: 1.5px; text-align: center; color: white; }

@media screen and (min-width: 500px) { .block-content .block-with-employee .parent .child .text h3 { font-size: 15px; font-weight: 500; font-stretch: normal; font-style: normal; line-height: normal; letter-spacing: 0.7px; text-align: center; color: #ffffff; } }

.block-content .block-with-employee .parent .child .text p { margin: 5px 0; font-family: "Roboto", sans-serif; font-size: 17px; font-weight: 500; font-stretch: normal; font-style: normal; line-height: 1.6; letter-spacing: 0.85px; text-align: center; color: white; }

@media screen and (min-width: 500px) { .block-content .block-with-employee .parent .child .text p { font-size: 13px; font-weight: 500; font-stretch: normal; font-style: normal; line-height: normal; letter-spacing: 0.7px; text-align: center; color: #ffffff; } }

@media screen and (min-width: 500px) { .block-content .block-with-employee .parent .child { min-height: 138px; } }

.block-content .block-with-employee .parent .child::before { content: ""; display: block; height: 100%; width: 100%; position: absolute; top: 0; left: 0; background: rgba(0, 0, 0, 0.6); }

@media screen and (min-width: 500px) { .block-content .block-with-employee .parent { width: 30%; height: 138px; } }

.block-content .block-with-employee .parent:hover .child, .block-content .block-with-employee .parent:focus .child { background-size: 250%; }

.block-content .block-with-employee .container { margin: 0 auto; padding: 10px 0; }

.block-content .block-with-employee .container .block-with-rools h3 { font-family: "Roboto", sans-serif; font-size: 24px; font-weight: 400; font-stretch: normal; font-style: normal; line-height: 1.6; letter-spacing: 1.5px; color: white; }

@media screen and (min-width: 500px) { .block-content .block-with-employee .container .block-with-rools h3 { font-size: 20px; } }

.block-content .block-with-employee .container .block-with-rools ol li { font-family: "Roboto", sans-serif; font-size: 18px; font-weight: 400; font-stretch: normal; font-style: normal; line-height: 1.6; letter-spacing: 1.5px; color: white; }

@media screen and (min-width: 500px) { .block-content .block-with-employee .container .block-with-rools ol li { font-size: 15px; } }

@media screen and (min-width: 500px) { .block-content .block-with-employee .container .block-with-rools { width: 50%; } }

.block-content .block-with-employee .container .block-faq { margin: 10px 0; }

.block-content .block-with-employee .container .block-faq .collapsible { background-color: #252525; color: white; cursor: pointer; padding: 18px; width: 100%; border: none; text-align: left; outline: none; font-family: "Roboto", sans-serif; font-size: 18px; font-weight: 500; font-stretch: normal; font-style: normal; line-height: 1.6; letter-spacing: 1.5px; }

@media screen and (min-width: 500px) { .block-content .block-with-employee .container .block-faq .collapsible { font-size: 15px; } }

.block-content .block-with-employee .container .block-faq .collapsible:after { content: "\002B"; color: white; font-weight: bold; float: right; margin-left: 5px; }

.block-content .block-with-employee .container .block-faq .active:after { content: "\2212"; }

.block-content .block-with-employee .container .block-faq .active, .block-content .block-with-employee .container .block-faq .collapsible:hover { background-color: #181818; }

.block-content .block-with-employee .container .block-faq .content { padding: 0 18px; max-height: 0; overflow: hidden; transition: max-height 0.2s ease-out; background-color: #f1f1f1; background: white; }

.block-content .block-with-employee .container .block-faq .content p { font-family: "Roboto", sans-serif; font-size: 15px; font-weight: 500; font-stretch: normal; font-style: normal; line-height: 1.6; letter-spacing: 1.5px; }

@media screen and (min-width: 500px) { .block-content .block-with-employee .container .block-faq .content p { font-size: 13px; } }

@media screen and (min-width: 500px) { .block-content .block-with-employee .rools-container { margin: initial; width: 100%; display: flex; } }

@media screen and (min-width: 500px) { .block-content .block-with-employee { flex-direction: row; flex-wrap: wrap; justify-content: space-between; overflow-x: auto; max-height: 500px; } }

@media screen and (min-width: 500px) { .block-content { margin-bottom: 45px; } }

.foot .start-game { display: flex; align-items: center; position: relative; min-height: 360px; background-image: url("../img/image.jpg"); background-position: center; background-repeat: no-repeat; background-size: cover; }

.foot .start-game .text { margin: 0 auto; z-index: 1; }

.foot .start-game .text h3 { text-transform: uppercase; font-family: "Roboto", sans-serif; font-size: 24px; font-weight: normal; font-stretch: normal; font-style: normal; line-height: normal; letter-spacing: 1.2px; text-align: center; color: #ffffff; }

@media screen and (min-width: 500px) { .foot .start-game .text h3 { font-size: 15px; font-weight: normal; font-stretch: normal; font-style: normal; line-height: normal; letter-spacing: 0.65px; text-align: center; } }

.foot .start-game .text a { text-decoration: none; }

.foot .start-game .text a .button { transition: all 0.5s; display: flex; justify-content: center; align-items: center; color: black; width: 256px; height: 73px; margin: 0 auto; background-color: #ffffff; font-family: "Roboto", sans-serif; font-size: 28px; font-weight: normal; font-stretch: normal; font-style: normal; line-height: normal; letter-spacing: 1.2px; text-align: center; }

@media screen and (min-width: 500px) { .foot .start-game .text a .button { font-size: 15px; width: 225px; height: 45px; } }

.foot .start-game .text a .button:focus, .foot .start-game .text a .button:hover { transform: scale(1.1); }

@media screen and (min-width: 500px) { .foot .start-game { min-height: 256px; } }

.foot .start-game::before { content: ""; display: block; height: 100%; width: 100%; position: absolute; top: 0; left: 0; background: rgba(0, 0, 0, 0.5); }

.wrapper { width: 67.5%; max-width: 768px; margin: 0 auto; }

@media screen and (min-width: 500px) { .wrapper { width: 78%; max-width: 1440px; } }

.active-button { color: white !important; }

.empty { margin: 0 !important; height: 0 !important; }

.slick-prev { left: 2% !important; z-index: 1; }

.slick-next { right: 2% !important; z-index: 1; }

*::-webkit-scrollbar-track { -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); background-color: #f5f5f5; }

*::-webkit-scrollbar { width: 6px; background-color: #f5f5f5; }

*::-webkit-scrollbar-thumb { background-color: #000000; }

